// Extra small devices (portrait phones, less than 576px)
// No media query for `xs` since this is the default in Bootstrap


// Small devices (portrait phones,less than 576px)
@media (max-width: 575px) {}

// Medium devices (landscape phones, less than 768px and up)
@media only screen and (min-width: 576px) and (max-width: 767px) {}

@media only screen and (max-width: 768px) {
    #footer_nav {
        display: none;
    }

    #top_header_area {
        display: none;
    }
}

// Large devices (tablets,less than 992px and up)
@media only screen and (min-width: 768px) and (max-width: 991px) {

    .slider_main {
        width: 80% !important;
    }

    .child_feature_area {
        width: 47% !important;
    }
}

@media only screen and (min-width: 768px) {
    .footer {
        display: flex;

        .footer-widgets {
            flex: 1;
        }
    }

}

@media only screen and (max-width: 768px) {}

// Extra large devices (desktops,less than 1200px and up)
@media only screen and (max-width: 991px) {
    #header_area {
        background-color: rgba(63, 63, 63, 0.56);

        .container {
            width: 100%;
        }

        .navbar-toggler {
            color: white;
            background-color: rgba(63, 63, 63, 0.9);
        }

        ul#nav {
            text-align: center;
            margin: 0;

            li {
                list-style: none;
                display: inline-block;

                a {
                    font-size: 16px;
                    text-transform: uppercase;
                    font-weight: bold;
                    display: block;
                    padding: 10px 10px;

                    &::after {
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        width: 0;
                        height: 2px;
                        content: '';
                        background-color: $menu-hover-color;

                        transition: .5s;
                    }

                    &:hover::after {
                        width: 100%;

                    }

                    &:hover {
                        color: $menu-hover-color;
                    }
                }
            }

        }

        .sub_menu{
            .sub_menu_child{
                position: static !important;
                width: 100% !important;


                li{
                    text-align: center;
                    width: 100%;


                }

            }
        }


    }
    .about-brave-content {
        .container {
            .row {
                border-top: 1px solid $brave-content-border;

                .col-lg-6 {
                    padding-top: 20px;
                    text-align: center;

                }
            }
        }
    }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {}
