@import "custom-variables";
// Your custom styles

@import url('https://fonts.googleapis.com/css?family=Muli:400,700,800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Poppins:400,700,800&display=swap');
$poppins : 'Poppins', sans-serif;

/*
	Common  Css
 */
*{
	margin: 0 ;
	padding: 0 ;
}

a{
	transition: .2s;
	text-decoration: none;
}
h1,h2,h3,h4,h5,h6,p{
	font-family: $poppins;

}

h1{
	font-size: 40px;
}
h2{
	font-size: 34px;
	color: #343546;
}
h3{
	font-size: 32px;
}
h4{
	font-size: 14px;
}
h5{
	font-size: 24px;

}
h6{
	font-size: 20px;
	color: #7c7c7c!important;


}
a.btn{
	font-size: 14px;
	padding: 8px 16px;
	color: #ffffff;
	background-color: #ff4530;
	border-radius: 20px;
	margin: 10px 0 10px 30px;
	text-transform: uppercase;

}
a{
	color: #fb542b;
	text-decoration: none;
	&:hover{
		text-decoration: underline;
		color: #fb542b;
	}
}




.navbar-toggler-icon{
	background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(0, 0, 0, 0.9)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e")

}



/*
	Top Header Area
 */

#header_area{
	background-color: $header-bg-color;
	.navbar-brand img{
		height: 70px;
	}

	ul#nav{
		text-align: right;
		margin: 0;

		li{
			list-style: none;
			display: inline-block;

			a{
				color: $header-nav-color;
				font-size: $nav-text-size;
				text-transform: uppercase;
				font-weight: bold;
				display: block;
				padding: 35px 10px;
				position: relative;
				font-family: $nav-font-family;
				transition: .5s;
				&::after{
					position: absolute;
					left: 0;
					bottom: 0;
					width: 0;
					height: 5px;
					content: '';
					background-color: $header-nav-hover-color;

					transition:.5s;
				}
				&:hover::after{
					width: 100%;

				}
				&:hover{
					color: $menu-hover-color;
					text-decoration: none !important;
				}
			}
			a.sub_menu_link{
				position: relative;
				padding-right: 20px;
				text-decoration: none;

				&::before{
					position: absolute;
					right: 0;
					top: 50%;
					content: "\f107";
					width: 10pt;
					height: 10px;
					font-family: FontAwesome;
					font-size: 14px;
					font-weight: bold;
					color: $header-nav-color;
					margin-top: -10px;
					transition: .5s;
				}
				&:hover::before{
					color: $menu-hover-color;;
				}

			}

		}
		.sub_menu{
			.sub_menu_child{
				display: none;
				position: absolute;
				width: 120px;


				li{
					display: block;
					background-color: rgba(0, 0, 0, 0.58);
					text-align: left;
					a{
						padding: 15px 10px;
					}

				}

			}
		}
	}


	&.scrolled {
		background-color: rgba(28,28,28,0.88);
		transition:padding 1s ease-in-out;
		.navbar-brand img{
			height: 70px;
		}
		ul#nav {
			li{
				a{
					padding: 30px 10px;
				}
				a.sub_menu_link{
					padding: 30px 20px 30px 10px;
				}
			}
		}

	}
}

/*
	Home Page Banner Area
 */
#banner-area{
	background: url("../img/home_3-compressed.jpg") no-repeat center top scroll;
	background-size: cover;

	.banner-content{
		text-align: center;
		color: white;
		padding-top: 300px;

		h1{
			font-size: 62px;
			font-weight: bold;
			padding-bottom: 20px;


		}
		p{
			font-size: 26px;
			font-weight: 500;
			letter-spacing: 1px;
		}
		a.btn{
			font-size: 19px;
			color: #ffffff;
			background-color: #ff4530;
			margin-top: 20px;
			margin-bottom: 50px;
			border-radius: 30px;

		}
		a.btn-transparent{
			background: transparent !important;
			padding: 12px 16px;
			transition: .5s;

			&:hover{
				background: #ffffff !important;
				color: #ff4530;

			}
		}
		.scrol_down{
			margin: 50px 0;

			span{
				display: inline-block;
				height: 40px;
				width: 40px;
				border: 3px solid #6b6872;
				border-radius: 20px;
				i{

					font-size: 22px;
					padding: 6px 5px;
					color: #6b6872;

				}
			}

		}
	}
}

